  .navbar {
    background: #fff;
  }
  
  /* ---------------------------------------------------
      SIDEBAR STYLE
  ----------------------------------------------------- */
  
  #sidebar {
    width: 250px;
    position: fixed;
    top: 0;
    left: -250px;
    height: 100vh;
    z-index: 999;
    background: #fff;
    color: #4c4c4c;
    transition: all 0.3s;
    overflow-y: scroll;
  }
  
  #sidebar.active {
    left: 0;
  }
  
  #dismiss {
    /* width: 35px;
    height: 35px;
    line-height: 35px; */
    font-size: 25px;
    cursor: pointer;
    text-align: center;
    color: #ee8011;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
  
  #dismiss:hover {
    background: #fff;
    color: #ee8011;
  }
  
  .overlay {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    z-index: 998;
    opacity: 0;
    transition: all 0.5s ease-in-out;
  }
  
  .overlay.active {
    display: block;
    opacity: 1;
  }
  
  .btn-warning {
    background-color: #ee8011 !important;
  }
  
  #sidebar .sidebar-header {
    padding: 10px;
    background: #ffff;
  }
  
  #sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #dcdee3;
  }
  
  #sidebar ul p {
    color: #fff;
    padding: 10px;
  }
  
  #sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
  }
  
  #sidebar ul li a:hover {
    color: #ee8011;
    background: #fff;
  }
  
  #sidebar ul li.active>a,
  a[aria-expanded="true"] {
    color: #fff;
    background: #ee8011;
  }
  
  a[data-toggle="collapse"] {
    position: relative;
  }
  
