  }

  .expose {
    -webkit-animation: reveal 1s ease forwards;
    animation: reveal 1s ease forwards;
  }

  .start-now {
    background: none;
    /* color: #0066f5; */
    box-shadow: none;
    padding: 0;
    font-size: 12.5px;
    text-transform: uppercase;
    letter-spacing: .7px;
    font-weight: 600;
  }

