    height: 12px;
    width: 12px;
    /* fill: #0066f5;
    stroke: #0066f5; */
    transition: transform .4s ease;
    margin-left: 2px;
    vertical-align: baseline;
    margin-bottom: -.1em;
  } 

  .start-now:hover svg {
      transform: translateX(5px);
  }

  .card-img-container {
    position: absolute;
    bottom: -240px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

