  }
  
  /* Browser mockup code
   * Contribute: https://gist.github.com/jarthod/8719db9fef8deb937f4f
   * Live example: https://updown.io
   */
  .browser-mockup {
    border-top: 50px solid #f7f7f7;
    position: relative;
    border-radius: 3px 3px 0 0;
    -webkit-box-shadow: -5.665px 9.429px 35px 0px rgba(0, 0, 0, 0.2);
    box-shadow: -5.665px 9.429px 35px 0px rgba(0, 0, 0, 0.2);
  }
  
  .browser-mockup:before {
    display: block;
    position: absolute;
    content: '';
    top: -29px;
    left: 30px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #f44;
    -webkit-box-shadow: 0 0 0 2px #f44, 1.5em 0 0 2px #9b3, 3em 0 0 2px #fb5;
    box-shadow: 0 0 0 2px #f44, 1.5em 0 0 2px #9b3, 3em 0 0 2px #fb5;
  }
  
  .browser-mockup.with-tab:after {
    display: block;
    position: absolute;
    content: '';
    top: -50px;
    left: 115px;
    width: 20%;
    height: 0rem;
    border-bottom: 50px solid white;
    border-left: 1.5em solid transparent;
    border-right: 1.5em solid transparent;
  }
  
  .browser-mockup.with-url:after {
    display: block;
    position: absolute;
    content: '';
    top: -1.6rem;
    left: 5.5rem;
    width: calc(100% - 6em);
    height: 1.2rem;
    border-radius: 2px;
    background-color: white;
  }
  
  .browser-mockup > * {
    display: block;
  }
  
  .blockquote {
    line-height: 1.7rem;
    color: #656565;
    padding: 40px;
    background: #fff;
    -webkit-box-shadow: -5.665px 9.429px 35px 0px rgba(0, 0, 0, 0.2);
    box-shadow: -5.665px 9.429px 35px 0px rgba(0, 0, 0, 0.2);
  }
  
  .blockquote p::first-letter {
    font-size: 2.5rem;
    color: #5989e5;
    font-weight: 800;
  }
  
  .modal {
    background: rgba(0, 0, 0, 0.8);
  }
  
  .modal button.close {
    width: 40px;
    height: 40px;
    line-height: 40px;
    color: #fff;
    text-align: center;
    background-color: #5989e5;
    background-image: -webkit-gradient(linear, left top, right top, from(#5989e5), to(#37cfdc));
    background-image: linear-gradient(to right, #5989e5, #37cfdc);
    position: absolute;
    top: 0;
    right: 0;
    opacity: 1;
  }
  
  .modal-content {
    border-radius: 0;
  }
  
  .modal-header,
  .modal-body {
    padding: 20px 30px;
    border: none;
  }
  
